import React, { Component } from 'react';
import Routing from './Routing.js';
import axios from 'axios';
import './App.css';

class ComboCreator extends Component {
    constructor(props) {
        super(props);
        this.state = {
            gameName: '',
            games: []
        }
    }

    pushRequest = () => {
        var nameGame = document.getElementById('nameGame').value;
        axios.post(`/addGame/${this.state.gameName}`);
    }

    setStates = (event) => {
        this.setState({ [event.target.name]: event.target.value });
    }

    handleSubmit = (event) => {
        console.log('A game name was submitted: ' + this.state.gameName);
        event.preventDefault();
    }

    render() {
        return (
            <div className="body">
                <form onSubmit={this.handleSubmit} className="form-inline">
                    <div className="container">
                        <br />
                        <div className="row">
                            <div className="col-12">
                                <label>
                                    <h1 id="TekkenFont">Combo Creator</h1>
                                </label>
                            </div>
                        </div>
                        <br />
                        <div className="row">
                            <div className="col-6">
                                <label>
                                    <input type="text" name="gameName" target='one' className="form-control inputArea" onChange={this.setStates} gameName={this.state.gameName} id="nameGame" placeholder="Add New Game *"></input>
                                </label>
                            </div>

                            <br />
                            <div className="row">
                            <div className="col-12">
                                <label>
                                    <input type="submit" value="Submit" onClick={this.pushRequest} />
                                </label>
                            </div>
                        </div>

                        </div>
                    </div>
                </form>
            </div>
        );
    }
}

export default ComboCreator;